import './messenger.css'
import Topbar from "../../components/topbar/Topbar"
import Conversation from '../../components/conversations/Conversation'
import Message from '../../components/message/Message'
import ChatOnline from '../../components/chatOnline/ChatOnline'
import { useState,useEffect,useRef} from 'react'
import axios from 'axios'
import {io} from 'socket.io-client'
import { useLocation } from 'react-router-dom'
import InfiniteScroll from 'react-infinite-scroll-component';
import { CircularProgress } from '@material-ui/core'
import { message } from 'statuses'
import InfiniteScrollReverse from "react-infinite-scroll-reverse";
export default function Messenger() {
    let location = useLocation();
    const user = JSON.parse(localStorage.getItem("user"))
    const [conversations,setConversations] = useState([]);
    const [currentChat,setCurrentChat] = useState(null);
    const [currentData,setCurrentData] = useState(null);
    const [chatQuery,setChatQuery] = useState("m");
    const [messages,setMessages] = useState([]);
    const [messagePage,setMessagePage] = useState(1);
    const [messageHasMore,setMessageHasMore] = useState(true);
    const [onlineUsers,setOnlineUsers] = useState([]);
    const [arrivalMessage,setArrivalMessage] = useState(null);
    const newMessage = useRef("");
    const socket = useRef();
    const scrollRef = useRef();
    useEffect(()=>{
        console.log("try again")
        socket.current = io(process.env.REACT_APP_SOCKET_PORT,{ transports: ['websocket', 'polling', 'flashsocket'] }) //andqport 
        socket.current.on("getMessage",data=>{
            setArrivalMessage({
                sender: data.senderId,
                text: data.text,
                createdAt: Date.now(),
            })
        })
    },[])

    useEffect(()=>{
	console.log("")
        arrivalMessage && currentChat?.members.includes(arrivalMessage.sender)&&
        setMessages(prev=>[...prev,arrivalMessage])
    },[arrivalMessage,currentChat])

    useEffect(()=>{
        console.log("try")
        socket.current.emit("addUser",user._id)
        socket.current.on("getUsers",users=>{
            console.log("",users)
            setOnlineUsers(
                user.friends.filter((f)=> users.some((u)=>u.userId===f))
            );
        })
    },[])
    
    useEffect(()=>{
        if(currentChat){
            const getMessages = async()=>{
                try{
                    const res = await axios.get('/api/messages/'+currentChat._id+"/"+0)
                    setMessages(res.data.data)
                }catch(err){
                    console.log(err)
                }
            };
            getMessages();
        }else{
            console.log("")
        }
    },[currentChat]);
    useEffect(()=>{
        const getConversations = async()=>{
            try{
                const res = await axios.get('/api/conversations/'+user._id)
                setConversations(res.data.data)
            }catch(err){
                console.log(err)
            }
            
        }
        getConversations()
    },[user._id])
    useEffect(()=>{
        scrollRef?.current?.scrollIntoView({behavior:"smooth"});
    },[messages])
    const handleSubmit = async(e)=>{
        e.preventDefault();
        console.log(newMessage.current.value)
        const message = {
            sender: user._id,
            text: newMessage.current.value,
            conversationId : currentChat._id,
        };
        const receiverId = currentChat.members.find(
            (member)=> member !== user._id
        );

        socket.current.emit("sendMessage",{
            senderId: user._id,
            receiverId,
            text:newMessage.current.value,
        })
        console.log(user._id)
        console.log(receiverId)
        try{
            const res = await axios.post("/api/messages",message);
            setMessages([...messages,res.data.data])
            newMessage.current.value=""
            if(!onlineUsers.includes(receiverId)){
                const  sendNotice = async ()=>{
                    const notice = {
                        senderId : user._id,
                        object : "message",
                        senderPic : user.profilePicture,
                        senderUsername : user.username,
                        receiverId
                    }
                    await axios.post('/api/notice',notice)
                }
                sendNotice()
            }
        }catch(err){
            console.log(err)
        }
    };
    useEffect(()=>{
        try{
            if (location.state.chat){
                console.log(location.state.chat)
                switchChat(location.state.chat)
                const test = sessionStorage.getItem('reload', 0);
                if (test<2){
                    sessionStorage.setItem('reload', test + 1);
                    console.log("count")
                    window.location.reload();
                }else{
                    
                    sessionStorage.removeItem('reload');
                }
            }else{
                console.log("wrong")
            }
        }catch(err){
            console.log(err)
        }
    },[])
    const enterSubmit = async(event)=>{
        if(event.key === 'Enter'){
            handleSubmit(event)
        }
    }
    const switchChat = (c)=>{
        setCurrentChat(c)
        //chat ( own ownuser.pc
        //user message request
        const friendId = c.members.find(m=> m !== user._id);
        const getUser = async()=>{
            try{
                const res = await axios(`/api/users?userId=${friendId}`)
                setCurrentData(res.data.data)
            }catch(err){
                console.log(err)
            }
        }
        getUser();
    }
    const fetchMessageData = async()=>{
        const getMessages = async()=>{
            try{
                 const res = await axios.get('/api/messages/'+currentChat._id+"/"+messagePage)
                 console.log(res.data.data)
                 if(res.data.data.length===0){
                     setMessageHasMore(false)
                 }
                 
                 setMessages(prevMessages=>{
                     return([...prevMessages,...res.data.data])
                 });
                 console.log(messages)
                 setMessagePage(messagePage+1)
                 console.log(messagePage)
            }catch(err){
                console.log(err)
            }
        } 
        getMessages()
    }
    return (
        <>
        <Topbar/>
        <div className="messenger">
            <div className="chatMenu">
                <div className="chatMenuWrapper">
                    {/* <input placeholder="" className="chatMenuInput" type="text" /> */}
                    <h4 className="chatSidebarTitle"></h4>
                    <hr/>
                    {/* queryfilter? */}
                    {conversations.map((c)=>(
                        <div onClick={()=>switchChat(c)} key={c._id}>
                            <Conversation conversation={c} currentUser={user} />
                        </div>
                    ))}
                </div>
            </div>
            <div className="chatBox">
                <div className="chatBoxWrapper">
                    {
                        currentChat
                    ?    
                    <>
                    <div className="chatBoxTop"  id="scrollableDiv" >
                        {/* infintite scroll */}
                        {/* <InfiniteScroll
                        dataLength={messages.length} //This is important field to render the next data
                        next={()=>fetchMessageData()}
                        hasMore={messageHasMore}
                        loader={<div className="onLoading"><CircularProgress  size="20px"/></div>}
                        
                        scrollableTarget="scrollableDiv"
                        // endMessage={
                        // <p style={{ textAlign: 'center' }}>
                        //     <b> "" </b>
                        // </p>
                        // }
                        > */}
                        {messages.map((m)=>(
                            <div ref={scrollRef} key={m._id} >
                            <Message message={m} own={m.sender === user._id} data={m.sender === user._id ?user:currentData} />
                            </div>
                        ))}
                        {/* </InfiniteScroll> */}
                        
                    </div>
                    <div className="chatBoxBottom">
                        <textarea 
                            className="chatMessageInput" 
                            placeholder="..."
                            ref = {newMessage}
                            defaultValue  = {newMessage.current.value}
                            onKeyPress={enterSubmit}
                        >
                        </textarea>
                        <button className="chatSubmitButton" onClick={handleSubmit}></button>
                    </div>
    
                    </>
                    :(
                        <span className="noConversationText"></span>
                    )}
                </div> 
            </div>
            <div className="chatOnline">
                <div className="chatOnlineWrapper">
                <h4 className="chatSidebarTitle"></h4>
                <hr/>
                    <ChatOnline 
                        onlineUsers={onlineUsers} 
                        currentId={user._id} 
                        setCurrentChat= {setCurrentChat}
                        switchChat = {switchChat}
                    />
                </div>
            </div>
        </div>
        </>
    )
}
